import { Injectable } from '@angular/core';

import { FieldConfig } from '../api/field.config';
import { StructureId } from '../../../core/api/structure.id';
import { InitFieldsCommand } from './init/init-fields.command';
import { CommandDispatcher } from '@generic-ui/hermes';


@Injectable()
export class FieldDispatcher {

	constructor(private readonly commandDispatcher: CommandDispatcher) {
	}

	initFields(fieldConfigs: Array<FieldConfig>, structureId: StructureId): void {
		this.commandDispatcher.dispatch(new InitFieldsCommand(structureId, fieldConfigs));
	}

}
