import { Injectable } from '@angular/core';

import { VerticalFormation } from './vertical-formation';
import { VerticalFormationModel } from '../domain-read/vertical-formation.model';


@Injectable()
export class VerticalFormationConverter {

	convert(verticalFormation: VerticalFormation): VerticalFormationModel {

		const enabled = verticalFormation.isEnabled(),
			topMargin = verticalFormation.getTopMargin(),
			sourceHeight = verticalFormation.getSourceHeight(),
			viewportHeight = verticalFormation.getViewPortHeight(),
			rowHeight = verticalFormation.getRowHeight();

		return new VerticalFormationModel(enabled, topMargin, sourceHeight, viewportHeight, rowHeight);
	}

}
