import { CellEditConfig } from '../../../../../../structure/grid/core/api/edit/cell-edit.config';
import { GuiCellEdit } from '../../../../core/api/gui.grid.public-api';


export class GuiGridCellEditConverter {

	convert(config: boolean | GuiCellEdit): CellEditConfig {

		if (typeof config === 'boolean') {
			return {
				enabled: config
			};
		}

		return config;
	}
}
