import { Injectable } from '@angular/core';

import { FieldCollection } from './field-collection';
import { FieldEntityFactory } from './field/field.entity-factory';


@Injectable()
export class FieldCollectionFactory {

	constructor(private readonly fieldFactory: FieldEntityFactory) {
	}

	create(): FieldCollection {
		return new FieldCollection(this.fieldFactory);
	}

}
