import { Injectable } from '@angular/core';

import { HermesObservable, HermesSingle, singleFromObservable } from '@generic-ui/hermes';

import { StructureId } from '../../../core/api/structure.id';
import { PagingRepository } from './paging.repository';
import { PagingModel } from '../api/paging.model';
import { PagingWarehouse } from '../api/paging.warehouse';


@Injectable()
export class PagingDomainWarehouse extends PagingWarehouse {

	constructor(private readonly pagingRepository: PagingRepository) {
		super();
	}

	onPaging(structureId: StructureId): HermesObservable<PagingModel> {
		return this.pagingRepository.on(structureId);
	}

	oncePaging(structureId: StructureId): HermesSingle<PagingModel> {
		return singleFromObservable(this.pagingRepository.on(structureId));
	}

}
