import { Injectable } from '@angular/core';
import { StructureReadModelRepository } from '../../../grid/core/api/read/structure.read-model-repository';
import { StructureId } from '../../../core/api/structure.id';
import { PagingModel } from '../api/paging.model';
import { StructureReadModelRoot } from '../../../grid/core/api/read/structure.read-model-root';
import { hermesFilter, hermesMap, HermesObservable } from '@generic-ui/hermes';


@Injectable()
export class PagingRepository {

	constructor(private readonly structureRepository: StructureReadModelRepository) {
	}

	on(structureId: StructureId): HermesObservable<PagingModel> {

		return this.structureRepository
				   .on(structureId)
				   .pipe(
					   hermesFilter((structure: StructureReadModelRoot) => structure.getId().toString() === structureId.toString()),
					   hermesMap((structure: StructureReadModelRoot) => structure.getPaging())
				   );
	}

}
