import { Injectable } from '@angular/core';

import { DomainEventHandler, DomainEventType } from '@generic-ui/hermes';

import { StructureId } from '../../../../core/api/structure.id';
import { FilterTypeArchive } from './filter-type.archive';
import { FilterTypeCollectionModel } from '../../api/type/filter-type-collection.model';
import { FilterTypesInitedEvent } from '../../domain/type/init/filter-types-inited.event';
import { FilterTypeModel } from '../../api/type/filter-type.model';


@Injectable()
export class FilterTypeConfigFilterSetEventHandler implements DomainEventHandler<StructureId, FilterTypesInitedEvent> {

	constructor(private readonly filterTypeArchive: FilterTypeArchive) {
	}

	forEvent(): DomainEventType<FilterTypesInitedEvent> {
		return FilterTypesInitedEvent;
	}

	handle(filterTypesInitedEvent: FilterTypesInitedEvent): void {

		if (filterTypesInitedEvent.ofMessageType('FilterTypesInitedEvent')) {

			const map: Map<string, Array<FilterTypeModel>> = filterTypesInitedEvent.getMap();

			this.filterTypeArchive.next(filterTypesInitedEvent.getAggregateId(), new FilterTypeCollectionModel(map));
		}
	}

}
