import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, Inject, Injector, OnInit, ViewEncapsulation } from '@angular/core';

import { FabricDialogService, FabricDrawerService } from '@generic-ui/fabric';

import { FilterMenuComponent } from './filter-menu.component';
import { filterContainerToken } from '../../core/api/config/filter-container-token';
import { FilterContainerRef } from '../../core/api/config/filter-container-ref';
import { SmartComponent } from '../../../../common/cdk/component/smart-component';
import { FilterWarehouse } from '../../core/api/filter.warehouse';
import { StructureId } from '../../../core/api/structure.id';
import { ActiveFilterModel } from '../../core/api/active/active-filter.model';

@Component({
	selector: 'div[gui-filter-menu-trigger]',
	templateUrl: './filter-menu-trigger.component.html',
	encapsulation: ViewEncapsulation.None,
	changeDetection: ChangeDetectionStrategy.OnPush,
	styleUrls: [
		'../style/filter.ngx.scss'
	]
})
export class FilterMenuTriggerComponent extends SmartComponent implements OnInit {

	activeFiltersExist: boolean = false;

	constructor(detector: ChangeDetectorRef,
				elementRef: ElementRef,
				private readonly injector: Injector,
				private readonly drawerService: FabricDrawerService,
				private readonly fabricDialogService: FabricDialogService,
				private readonly structureId: StructureId,
				private readonly filterWarehouse: FilterWarehouse,
				@Inject(filterContainerToken) private readonly filterContainerRef: FilterContainerRef) {
		super(detector, elementRef);
	}

	ngOnInit() {

		this.subscribe(
			this.filterWarehouse.onActiveFilters(this.structureId),
			(activeFilters: ReadonlyArray<ActiveFilterModel>) => {
				this.activeFiltersExist = activeFilters.length > 0;
			}
		);
	}

	openDrawer(): void {
		const elementRef = this.filterContainerRef.getElementRef();

		this.fabricDialogService.open({ component: FilterMenuComponent, injector: this.injector });
		// this.drawerService.open(elementRef, FilterMenuComponent, { injector: this.injector });
	}

	protected getSelectorName(): string {
		return 'gui-filter-menu-trigger';
	}
}
