import { Injectable } from '@angular/core';

import { DomainEventBus } from '@generic-ui/hermes';

import { StructureId } from '../../../core/api/structure.id';
import { FieldCommandInvoker } from '../api/field.command-invoker';
import { FieldConfig } from '../api/field.config';
import { FieldDispatcher } from './field.dispatcher';


@Injectable()
export class FieldDomainCommandInvoker extends FieldCommandInvoker {

	constructor(private readonly domainEventBus: DomainEventBus,
				private readonly fieldDispatcher: FieldDispatcher) {
		super();
	}

	initFields(fieldConfigs: Array<FieldConfig>, structureId: StructureId): void {
		this.fieldDispatcher.initFields(fieldConfigs, structureId);
	}

}
